@font-face {
  font-family: "CeraPro-Regular";
  src: url("../../assets/fonts/CeraPro-Regular.woff") format("woff");
}

@font-face {
  font-family: "CeraPro-Light";
  src: url("../../assets/fonts/CeraPro-Light.woff") format("woff");
}

@font-face {
  font-family: "CeraPro-Bold";
  src: url("../../assets/fonts/CeraPro-Bold.woff") format("woff");
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  width: 100%;
  background: url('../../assets/images/greyBackground-1.jpg') center no-repeat;
  background-size: cover;
  font-family: "CeraPro-Light", sans-serif;
  font-weight: light;
  text-transform: uppercase;
  position: relative;
}

.Layout {
  min-height: 100%;
  box-sizing: border-box;
  margin: auto;
  padding: 30px;
}

.Content {
  margin: 5rem auto;
  width: 100%;
  box-sizing: border-box;
  margin-top: 3rem;
  text-align: center;
}

@media screen and (min-width: 800px) {
  .Content {
    margin: 10rem auto;
    width: 80%;
  }
}