.SideDrawer {
    position: fixed;
    width: 200px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background: linear-gradient(#808080, #000);
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

.Logo {
    filter: brightness(0.85);
    margin: auto;
    max-height: 100%;
    width: auto;
}

@media (min-width: 500px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    transform: translateX(0);
}

.Close {
    transform: translateX(-100%);
}

.Logo {
    height: 75px;
    width: 75px;
    margin-bottom: 32px;
}