.Footer {
  position: absolute;
  bottom: 0;
  margin: auto;
  width: calc(100% - 60px);
  display: flex;
  justify-content: center;
  font-size: 1rem;
  align-items: center;
}

.Footer i {
  margin: 1rem;
  color: #78797B;
}

.Footer i:hover {
  color: #EBD016;
}

@media screen and (min-width: 800px) {
  .Footer {
    flex-direction: column;
    align-items: flex-start;
  }

  .Footer i {
    margin: .9rem auto;
    font-size: 1rem;
  }
}

@media screen and (min-width: 1200px) {
  .Footer {}
}