.AboutContent img {
  opacity: .4;
  height: 300px;
}

.AboutContent {
  font-size: .8rem;
  color: #78797B;
  display: flex;
  flex-direction: column;
}

.AboutContent p {
  text-transform: initial;
}

.AboutContent div:last-of-type {
  padding: 30px;
}

@media screen and (min-width: 800px) {
  .AboutContent {
    flex-direction: row;
  }
}


@media screen and (min-width: 1200px) {
  .AboutContent {
    font-size: 18px;
  }

  .AboutContent img {
    height: 500px;
  }
}