.Contact {
  display: flex;
  padding: 30px;
  justify-content: center;
  flex-direction: column;
  color: white;
  box-shadow: 2px 3px 10px #000;
}

.Contact h2 {
  margin-bottom: 0;
}

.Contact h1 {
  font-size: 1.5rem;
}

.Contact a {
  color: #78797B;
  text-decoration: none;
}

.Contact a:hover {
  color: #EBD016;
}

.Contact p {
  margin: 5px auto;
  color: #78797B;
  font-size: .7rem;
  text-transform: initial;
}

button {
  color: #EBD016;
  height: 50px;
  width: 200px;
  margin: 20px auto;
  border: none;
  transition: .3s ease-in-out;
  background-color: #78797B;
  text-transform: uppercase;
  box-shadow: 2px 3px 10px #000;
}

button:hover {
  color: #78797B;
  background-color: #EBD016;
}

@media screen and (min-width: 500px) {
  .Contact {
    flex-direction: row;
  }

  .Contact div {
    width: 50%;
  }
}

@media screen and (min-width: 1200px) {
  .Contact {
    height: 500px;
    align-items: center;
  }

  .Contact p {
    font-size: 1.2rem;
    text-transform: initial;
  }

  .Contact h1,
  h2 {
    font-size: 2rem;
  }

  .Contact div {
    height: 360px;
  }
}