.z * {
  border: 1px solid red;
}

.Home {
  color: white;
}

.Home p {
  color: #78797B;
}

.Home p:first-of-type {
  padding-right: 13rem;
}

.Home p:last-of-type {
  padding-left: 6rem;
}

.Home h1 {
  font-family: "CeraPro-Bold", sans-serif;
  font-size: 3rem;
  margin: 10px auto;
}

@media screen and (min-width: 500px) {
  .Home {
    color: white;
    height: 100%;
    width: auto;
  }

  .Home p:first-of-type {
    text-align: center;
    padding-right: 350px;
  }

  .Home p:last-of-type {
    padding-left: 250px;
    text-align: center;
  }

  .Home h1 {
    font-size: 5rem;
  }
}

@media screen and (min-width: 800px) {
  .Home p {
    font-size: 1.5rem;
  }

  .Home h1 {
    font-size: 8rem;
  }
}

@media screen and (min-width: 1200px) {
  .Home {
    align-items: center;
  }

  .Home p {
    font-size: 2rem;
  }

  .Home h1 {
    font-size: 12rem;
  }

}